import Vue from 'vue'
import Router from 'vue-router'
import Home from './pages/home'
Vue.use(Router);

export default new Router({
  routes:[
    {
      path:'/',
      name:'home',
      component:Home,
    },
    {
      path: '/hupu',
      name: 'hupu',
      component: () => import('./pages/hupu.vue')
    },
    {
      path: '/bili',
      name: 'bili',
      component: () => import('./pages/bili.vue')
    },
    {
      path: '/douban',
      name: 'douban',
      component: () => import('./pages/douban.vue')
    },
  ]
});
