<template>
  <div class="home_container">
    <el-carousel :interval="2500" arrow="always" class="home_banner">
      <el-carousel-item
        v-for="(item, index) in demoList"
        :key="index"
        :style="{background: 'center url('+ item.image +') no-repeat' }"
        class="banner-item-wrapper">
        <div class="item-info">
          <p class="item-title">{{ item.name }}</p>
          <p class="item-desc">{{ item.desc }}</p>
        </div>
        <a :href="getPrefix() + item.url" class="item-url">去看看</a>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>
<script>
  import { DemoList } from '../congfig'
  export default{
    name:'home',
    components:{
    },
    data(){
      return {
        demoList: DemoList,
      }
    },
    mounted(){
      // this.init();
    },
    methods:{
      getPrefix(){
        let url = window.location.href;
        if (url.indexOf('demo') !== -1) {
          return '/demo/#'
        } else {
          return '/#'
        }
      },
    }
  }
</script>

<style lang="scss">
  @import "../assets/mixin";
  @import "../assets/config.scss";
  .home_container {
    @include wh();
    .home_banner {
      @include wh();
      .el-carousel__indicators--horizontal {
        bottom: 40px;
      }
      .el-carousel__container {
        @include wh();
      }
      .banner-item-wrapper {
        @include wh();
        @include flex_center();
        flex-direction: column;
        .item-url {
          margin-top: 40px;
          padding: 10px 40px;
          border: 2px solid;
          border-radius: 8px;
          font: 20px 'Roboto:100',sans-serif;
          transition: all .5s;
          color: $c_white;
          border-color: $c_white;
          background:-webkit-linear-gradient(left,$c_primary, $c_primary) no-repeat;
          background-size:0 100%;
          &:hover {
            background-size:100% 100%;
            color: $c_white;
          }
        }
        .item-info {
          @include flex();
          padding: 40px;
          border: 2px solid $c_white;
          .item-title {
            font-size: 40px;
            color: $c_white;
            margin-bottom: 20px;
          }
          .item-desc {
            font-size: $font20;
            color: $c_white;
            margin-bottom: 20px;
          }
        }
      }
    }
  }
</style>
